import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import OAuths from 'oauth-signature';
import { MARTJACK_FRONT_API_URL, MERCHANT_ID, CONSUMER_KEY, SECRET_KEY, LANG_CODE } from '../../config';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class MartjackauthConfigProvider {
  _header: HttpHeaders;
  oAuthData: OAuths;
  MARTJACK_DEV_URL: string;
  MARTJACK_IMAGE_URL: string;

  constructor(private http: HttpClient) {
  }

  getConfig() {
    this.http.get(`${MARTJACK_FRONT_API_URL}Config/${ MERCHANT_ID }?languagecode=${LANG_CODE}`).subscribe(data => {
      if (data['MessageCode'] == '200') {
        // this.MARTJACK_DEV_URL = data['Properties'].dev_api_url + `Customer/${MERCHANT_ID}/`;
        this.MARTJACK_DEV_URL = data['Properties'].dev_api_url;
        this.MARTJACK_IMAGE_URL = data['Properties'].imagepath;
      }
    }, err => {
      Observable.throw(err)
    });
  }

  getNounce(length) {
    let text = "";
    let possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    for (var i = 0; i < length; i++) {
      text += possible.charAt(Math.floor(Math.random() * possible.length));
    }
    return text;
  }

  getOuthSignature(requestType: string, url: string,oauth_token:any):string {
    let finalURL;
      const time = new Date().getTime();
      const nonce = this.getNounce(10);
      this.oAuthData = {
        oauth_consumer_key: CONSUMER_KEY,
        oauth_signature_method: 'HMAC-SHA1',
        oauth_timestamp: time,
        oauth_nonce: nonce,
        oauth_version: '1.0',
      }
      let consumerSecret = SECRET_KEY;
      let signature = OAuths.generate(requestType, this.MARTJACK_DEV_URL + url, this.oAuthData, consumerSecret);
      return finalURL =  '?oauth_consumer_key=' + this.oAuthData.oauth_consumer_key + '&oauth_token=' + oauth_token + '&' + 'oauth_signature_method=' + this.oAuthData.oauth_signature_method + '&' + 'oauth_timestamp=' + this.oAuthData.oauth_timestamp + '&oauth_nonce=' + this.oAuthData.oauth_nonce + '&oauth_version=' + this.oAuthData.oauth_version + '&oauth_signature=' + signature;
  }
}



