import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from "rxjs";

/*
  Generated class for the CartProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class CartProvider {

  isCartSubject=new BehaviorSubject<boolean>(this.cartItems());

  constructor(public http: HttpClient) {
    console.log('Hello CartProvider Provider');
  }

  setCartItemQuantity(Quantity) : void {
    localStorage.setItem('cartQuantity',Quantity);
    console.log( Quantity,":::::::::set cart no::::::::::::")
    console.log(localStorage.getItem('cartQuantity'),"::::::::::get cart no:::::::::::")
    this.isCartSubject.next(true);
  }

  public cartItems() : boolean {
    return !!localStorage.getItem('cartQuantity');
  }

  getCartItemQuantity() {
    return localStorage.getItem('cartQuantity');
  }

  isCartItem() : Observable<boolean> {
    return this.isCartSubject.asObservable();
  }

}



